/* Bookshelf form */
.book-list-wrapper {
    padding: 16px;
    margin: 64px auto 0;
    max-width: 768px;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.book-list-form {
    margin-bottom: 32px;
}

.form-row {
    display: flex;
    flex-wrap: wrap;
}

.form-row + .form-row {
    margin-top: 18px;
}

.form-wrapper fieldset {
    padding: 0;
    margin: 0;
    width: 50%;
    border: 0;
}

.form-wrapper fieldset:first-child {
    padding-right: 4px;
}

.form-wrapper fieldset:last-child {
    padding-left: 4px;
}

.form-label {
    display: block;
    margin-bottom: 6px;
    font-size: 12px;
    color: #2c3e50;
}

.form-input {
    padding: 8px;
    width: 100%;
    font-size: 14px;
    border: 1px solid #dedede;
    border-radius: 4px;
    transition: border-color .25s ease-out;
}

.form-input:focus {
    border-color: #3498db;
    box-shadow: 0 0 2px #3498db;
    outline: 0;
}

.btn,
.btn-remove,
.btn-reset {
    display: block;
    border: 0;
    cursor: pointer;
    transition: all .25s ease-out;
}

.btn-add {
    padding: 12px 18px;
    margin: 12px auto 0;
    font-size: 14px;
    font-weight: 700;
    color: #fff;
    background-color: #3498db;
    border-radius: 4px;
}

.btn-add:hover {
    background-color: #2980b9;
}

.btn-remove,
.btn-reset {
    padding: 0;
    font-size: 12px;
    font-style: italic;
    color: hsl(6, 63%, 46%);
    background-color: transparent;
}

.btn-remove:hover,
.btn-reset:hover {
    color: hsl(6, 63%, 26%);
}

.btn-reset {
    margin: 21px auto 0;
}